import React, { useState } from "react";
import axios from "axios";
import { useNavigate } from "react-router-dom";

function Register() {
  const navigate = useNavigate();
  const [name, setName] = useState("");
  const [email, setEmail] = useState("");
  const [password, setPassword] = useState("");
  const [mensaje, setMensaje] = useState("");

  const handleRegister = async (e) => {
    e.preventDefault();

    if (!name || !email || !password) {
      setMensaje("‚ùå Por favor completa todos los campos.");
      return;
    }

    try {
      const response = await axios.post("http://localhost:5297/api/Auth/register", {
        name,
        email,
        password,
      });

      if (response.status === 200) {
        setMensaje("‚úÖ Registro exitoso. Ser√°s redirigido al login...");
        setTimeout(() => navigate("/"), 1500);
      }
    } catch (error) {
      console.error(error);
      setMensaje("‚ùå El correo ya est√° registrado o hubo un error.");
    }
  };

  return (
    <div className="min-h-screen flex items-center justify-center bg-gradient-to-br from-gray-900 via-gray-800 to-gray-900 p-4">
      <div className="bg-white shadow-2xl rounded-2xl p-8 w-full max-w-md">

        {/* Logo */}
        <img src="/logo.png" alt="CodeHunt Logo" className="w-28 mx-auto mb-4 drop-shadow-lg" />

        {/* T√≠tulo */}
        <h2 className="text-3xl font-extrabold text-center text-gray-800 mb-6">
          Crear cuenta en <span className="text-indigo-600">CodeHunt</span>
        </h2>

        {/* Formulario */}
        <form className="space-y-5" onSubmit={handleRegister}>
          <div>
            <label className="block text-sm font-medium text-gray-700 mb-1">Nombre completo</label>
            <input
              type="text"
              required
              value={name}
              onChange={(e) => setName(e.target.value)}
              className="w-full px-4 py-2 border border-gray-300 rounded-lg shadow-sm focus:outline-none focus:ring-2 focus:ring-indigo-500 transition"
              placeholder="Juan P√©rez"
            />
          </div>

          <div>
            <label className="block text-sm font-medium text-gray-700 mb-1">Correo electr√≥nico</label>
            <input
              type="email"
              required
              value={email}
              onChange={(e) => setEmail(e.target.value)}
              className="w-full px-4 py-2 border border-gray-300 rounded-lg shadow-sm focus:outline-none focus:ring-2 focus:ring-indigo-500 transition"
              placeholder="correo@codehunt.com"
            />
          </div>

          <div>
            <label className="block text-sm font-medium text-gray-700 mb-1">Contrase√±a</label>
            <input
              type="password"
              required
              value={password}
              onChange={(e) => setPassword(e.target.value)}
              className="w-full px-4 py-2 border border-gray-300 rounded-lg shadow-sm focus:outline-none focus:ring-2 focus:ring-indigo-500 transition"
              placeholder="‚Ä¢‚Ä¢‚Ä¢‚Ä¢‚Ä¢‚Ä¢‚Ä¢‚Ä¢"
            />
          </div>

          <button
            type="submit"
            className="w-full bg-indigo-600 text-white py-2 rounded-lg hover:bg-indigo-700 transition-all duration-200 font-medium shadow-md"
          >
            Registrarse
          </button>
        </form>

        {mensaje && (
          <p className="mt-4 text-center text-sm text-red-600">{mensaje}</p>
        )}

        {/* Enlace para volver al login */}
        <p className="mt-4 text-center text-sm text-gray-500">
          ¬øYa tienes cuenta?{" "}
          <a href="/" className="text-indigo-600 hover:underline font-medium">
            Inicia sesi√≥n
          </a>
        </p>
      </div>
    </div>
  );
}

export default Register;
